import cuPanel from '@/components/custom/cu-panel.vue'
import cuCell from '@/components/custom/cu-cell.vue'
import cuDate from '@/components/custom/cu-date.vue'
import { api } from '@/config/common.js'
import { queryHotSellingProduct } from '@/api/data.js'
import { dateFormat, numberFormat } from '@/utils/tools.js'
export default {
	components: {
		cuPanel,
		cuCell,
		cuDate
	},
	data() {
		return {
			title: '热销商品分析',
			date: 'custom',
			days: 30,
			startDate: '',
			endDate: '',
			datas: [],
			initSort: [
				{ id: 0, name: '数量' },
				{ id: 1, name: '金额' }
			],
			initFilter: [
				{ id: 0, name: '产品分类', checked: true, condition: false },
				{ id: 1, name: '往来单位', checked: false, condition: true }
			],
			customerName: ''
		}
	},
	onLoad(options) {
		let sDate = new Date()
		let eDate = new Date()
		sDate.setDate(sDate.getDate() - 29)
		this.startDate = dateFormat('YYYY-mm-dd', sDate)
		this.endDate = dateFormat('YYYY-mm-dd', eDate)
		this.getData()
	},
	computed: {
		headerHeight() {
			return this.$headerHeight
		},
		initDate() {
			return {
				title: this.title,
				date: this.date,
				startDate: this.startDate,
				endDate: this.endDate
			}
		}
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleNavTo(url, params) {
			uni.navigateTo({
				url: url+'?'+params
			})
		},
		getData() {
			let	reqData = {
				startDate: this.startDate,
				endDate: this.endDate,
				customerName: this.customerName
			}
			this.$refs.loading.open()
			queryHotSellingProduct(api.report, reqData).then(res => {
				this.$refs.loading.close()
				if (res.status == 200 && res.data.returnCode == '0000') {
					this.datas = res.data.data.resultList
					this.customerName = ''
				}
			}).catch(error => {
				this.$refs.loading.close()
			})
		},
		numberFilter(number) {
			return numberFormat(number)
		},
		compare(prop, type) {
			if (type == 'up') {
				return (obj1, obj2) => {
					let val1 = obj1[prop]
					let val2 = obj2[prop]
					if (val1 < val2) {
						return -1
					} else if (val1 > val2) {
						return 1
					} else {
						return 0
					}
				}
			} else {
				return (obj1, obj2) => {
					let val1 = obj1[prop]
					let val2 = obj2[prop]
					if (val1 > val2) {
						return -1
					} else if (val1 < val2) {
						return 1
					} else {
						return 0
					}
				}
			}
		},
		sortData(type, val) {
			this.initSort = this.initSort.map((item)=>{
				if (item.id == val) {
					item.checked = true
				} else {
					item.checked = false
				}
				return item
			})
			if (type == 'up') {
				if (val == 0) {
					this.datas.sort(this.compare('qty', 'up'))
				} else {
					this.datas.sort(this.compare('amount', 'up'))
				}
			} else {
				if (val == 0) {
					this.datas.sort(this.compare('qty', 'down'))
				} else {
					this.datas.sort(this.compare('amount', 'down'))
				}
			}
		},
		handleDateOk(val) {
			this.date = val.date
			this.startDate = val.startDate
			this.endDate = val.endDate
			this.getData()
		},
		handleSortUp(val) {
			this.sortData('up', val)
		},
		handleSortDown(val) {
			this.sortData('down', val)
		},
		handleFilterOk(val) {
			this.initFilter = this.initFilter.map((item)=>{
				if (item.id == val) {
					item.checked = true
				} else {
					item.checked = false
				}
				return item
			})
			this.getData()
		},
		handleFilterCancel() {
			this.customerName = ''
		}
	}
}
